.basket {
  &__link {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-decoration: none;

    &:hover,
    &:focus {
      .basket__items-counter {
        box-shadow: 0.5px 2px 5px 0 rgba($red, 0.5);
      }
    }
  }

  &__icon {
    position: relative;
    width: 48px;
    height: 48px;
    border: 2px solid $gray-400;
    border-radius: 50%;
    background-image: url(data:image/svg+xml;charset=utf-8;base64,);
    background-position: center center;
    background-size: 24px 24px;
    background-repeat: no-repeat;
  }

  &__items-counter {
    position: absolute;
    bottom: 2px;
    left: 38px;
    display: block;
    min-width: 20px;
    height: 20px;
    padding: 0 4px;
    background-color: $red;
    border-radius: 10px;
    color: $white;
    font-family: "PT Sans", "Helvetica", "Arial", sans-serif;
    font-size: 11px;
    font-weight: bold;
    line-height: 20px;
    text-align: center;
    transform: translateX(-50%);
  }

  &__text {
    margin-left: 20px;
  }

  &__amount {
    color: $gray-700;
    font-family: "Montserrat", "Helvetica", "Arial", sans-serif;
    font-size: 17px;
    font-weight: normal;
  }

  &__description {
    display: block;
    margin-top: 5px;
    color: $gray-600;
    font-size: 13px;
  }
}
